.App {
  text-align: center;
}

.App-logo {
  height: 200px;
  background: #fff;
  border-radius: 50%;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.social-link {
    margin: 15px;
}

.social-link{
  padding: 10px 15px;
  font-size: 16px;
  border-radius: 30px;
  -webkit-transition-duration: .2s;
  -o-transition-duration: .2s;
  transition-duration: .2s;
  -webkit-transition-property: all;
  -o-transition-property: all;
  transition-property: all;
  -webkit-transition-timing-function: ease-in-out;
  -o-transition-timing-function: ease-in-out;
  transition-timing-function: ease-in-out;
}

.social-link > svg {
  margin-right: 10px;
}

.linkedin {
  background: #0077b5;
  color: #fff;
  border: 1px solid #fff;
}

.linkedin:hover {
  background: #fff;
  color: #0077b5;
  border: 1px solid #0077b5;
}

.github {
  background: #24292e;
  color: #fff;
  border: 1px solid #fff;
}

.github:hover {
  background: #fff;
  color: #24292e;
  border: 1px solid #24292e;
}

.filter, .lists {
  margin: 15px 0;
}

.label-filter>* {
  display: block;
  text-align: center;
  margin: 5px auto;
  padding: 5px;
}

.lists {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.wordpress, .prestashop, .javascript {
  padding: 30px 0;
  width: 33%;
  min-width: 250px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.wordpress, .javascript {
  background: #efefef;
}

.cat-img {
  margin: 10px;
}

.page-container {
  padding: 10px 0;
}

.page-container>* {
  margin: 10px;
}

.page-btn>button {
  padding: 10px 25px;
  background: #222;
  color: white;
  border: 1px solid #222;
  border-radius: 25px;
  -webkit-transition-duration: .2s;
  -o-transition-duration: .2s;
  transition-duration: .2s;
  -webkit-transition-property: all;
  -o-transition-property: all;
  transition-property: all;
  -webkit-transition-timing-function: ease-in-out;
  -o-transition-timing-function: ease-in-out;
  transition-timing-function: ease-in-out;
}

.page-btn>button:hover {
  color: #222;
  background: inherit;
}

@media screen and (max-width: 768px) {
  .wordpress, .prestashop, .javascript {
    width: 100%;
  }
}



